.align-items-center {
  align-items: center; }

.w-100 {
  width: 100%; }

.a-1 {
  width: 1em;
  height: 1em; }

.ml-1 {
  margin-left: 1em; }

.mt-0 {
  margin-top: 0; }

body {
  font-family: "Russo One", sans-serif;
  text-align: center; }

h1 {
  font-family: "Press Start 2P", cursive; }

button {
  font-family: "Press Start 2P", cursive;
  padding: 0.5em 1em;
  cursor: pointer; }

.container {
  max-width: 600px;
  margin: 0 auto; }

.board {
  margin: 1em;
  height: auto;
  flex: 1;
  max-width: 600px; }
  .board:before {
    content: '';
    padding-top: 100%;
    float: left; }

.row {
  display: flex;
  justify-content: space-around; }

.square {
  flex: 1;
  height: auto;
  color: white;
  text-align: center;
  background-color: darkcyan;
  border: 1px solid lightgrey;
  cursor: pointer; }
  .square:before {
    content: '';
    padding-top: 100%;
    float: left; }
  .square.ghost {
    background-color: grey; }
  .square.ship {
    background-color: darkblue; }
  .square.hit {
    background-color: red; }
  .square.miss {
    background-color: white; }
